.header {
	width: 100%;
	max-width: 880px;
	margin: 0 auto;
	border-bottom: 1px solid rgba(84, 84, 84, 0.7);
	box-sizing: border-box;
	padding: 0 0 41px 0;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

@media screen and (max-width: 920px) {
	.header {
		padding: 0 0 31.6px 27px;
	}
}